.navbar-vertical-nav {
    width: 250px;
    background-color: #ffffff;
    border-right: 1px solid #e0e0e0;
  }
  
  .navbar-vertical {
    height: 100vh;
    display: flex;
    flex-direction: column;
  }
  
  .navbar-vertical .navbar-brand img {
    max-width: 100%;
    height: auto;
  }
  
  .navbar-vertical-content {
    flex-grow: 1;
    overflow-y: auto;
  }
  
  .navbar-nav .nav-item .nav-link {
    display: flex;
    align-items: center;
    padding: 10px 15px;
    color: #333333;
    text-decoration: none;
  }
  
  .navbar-nav .nav-item .nav-link.active {
    background-color: #f1f1f1;
    border-left: 3px solid #007bff;
  }
  
  .navbar-nav .nav-item .nav-link .nav-link-icon {
    margin-right: 10px;
  }
  
  .navbar-nav .nav-item .nav-link .nav-link-text {
    flex-grow: 1;
  }
  
  .navbar-nav .nav-item .nav-link.disabled {
    color: #aaaaaa;
    cursor: not-allowed;
  }
  
  .navbar-nav .nav-item .nav-label {
    font-size: 14px;
    color: #666666;
    padding: 10px 15px;
    text-transform: uppercase;
    font-weight: bold;
  }
  
  .navbar-nav .nav-item .badge {
    margin-left: 10px;
  }
  
  .navbar-nav .nav-item.mt-6 {
    margin-top: 24px !important;
  }
  
  .navbar-nav .nav-item.mb-3 {
    margin-bottom: 12px !important;
  }
  