.main-content-wrapper {
    padding: 20px;
    margin-top: 100px;
  }
  
  .card {
    border-radius: 10px;
    overflow: hidden;
  }
  
  .card-lg {
    height: 100%;
  }
  
  .table-responsive-xl {
    overflow-x: auto;
  }
  
  .icon-shape {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .bg-light-danger {
    background-color: #f8d7da !important;
  }
  
  .text-dark-danger {
    color: #721c24 !important;
  }
  
  .bg-light-warning {
    background-color: #fff3cd !important;
  }
  
  .text-dark-warning {
    color: #856404 !important;
  }
  
  .bg-light-info {
    background-color: #d1ecf1 !important;
  }
  
  .text-dark-info {
    color: #0c5460 !important;
  }
  
  .bg-light-primary {
    background-color: #d6e0f5 !important;
  }
  
  .bg-primary {
    background-color: #007bff !important;
  }
  
  .bg-light-success {
    background-color: #d4edda !important;
  }
  
  .bg-success {
    background-color: #28a745 !important;
  }
  
  .bg-light-danger {
    background-color: #f8d7da !important;
  }
  
  .bg-danger {
    background-color: #dc3545 !important;
  }
  
  .text-success {
    color: #28a745 !important;
  }
  
  .text-warning {
    color: #ffc107 !important;
  }
  
  .text-danger {
    color: #dc3545 !important;
  }
  
  .avatar {
    width: 50px;
    height: 50px;
  }
  
  .avatar-md {
    width: 60px;
    height: 60px;
  }
  
  .progress {
    background-color: #f1f1f1;
  }
  
  .progress-bar {
    height: 6px;
  }
  
  .list-unstyled {
    padding-left: 0;
    list-style: none;
  }
  